<template>
    <div class="wrapper">
        <div class="simu-header">
            <div class="simu-header-box">
                <div class="accessory">
                    附件
                </div>
                <div class="count-down">{{countDown}}</div>
                <div class="current-state">
                    <div class="start">
                        <p class="start-desc">开始时间</p>
                        <p class="time">{{startTime}}</p>
                    </div>
                    <div class="state">
                        <p class="state-text">状态</p>
                        <p class="state-desc">未考</p>
                    </div>
                    <div class="end">
                        <p class="end-desc">结束时间</p>
                        <p class="time">{{endTime}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style scoped>
    @import './Simu.css';
</style>



<script>
import header from '../com/header'
import { setInterval, clearInterval } from 'timers';
export default {
    // props: {
    //     startTime: {
    //         type: String,
    //         required: true
    //     }
    // },
    components: {
        "z-header": header
    },
    data(){
        return {
            countDown: '1:00',
            seconds: 0,
            hours: 1
            // startTime: this.$route.query.startTime
        }
    },
    methods: {
        getStartTime(){
            const date = new Date();
            const hours = date.getHours();
            const minutes = date.getMinutes();
            const seconds = date.getSeconds();
            this.startTime = hours + ':' + minutes + ':' + seconds
        },
         _changeCountDown(seconds,hours){
            hours = parseInt(hours);
            // let seconds = 0;
            const timer = setInterval(() =>{
                if(seconds == 0){
                    seconds = 60;
                    hours -= 1;
                }
                seconds = seconds - 1;
                let stringSeconds = seconds;
                let stringHours = hours
                if(stringSeconds < 10){
                    stringSeconds = '0' + stringSeconds
                }
                if(stringSeconds == 60){
                    stringSeconds = '00'
                }
                if(stringHours < 10){
                    stringHours = '0' + stringHours
                }
                this.countDown = stringHours + ':' + stringSeconds;
                this.seconds = seconds;
                this.hours = hours;
                if(hours == 0 && seconds == 0){
                    clearInterval(timer)
                }
            },1000)
         }
    },
    computed: {
        startTime(){
            return this.$route.query.Time.startTime
        },
        endTime(){
            return this.$route.query.Time.endTime
        }
    },
    created(){
        this._changeCountDown(this.seconds,this.hours)
        // this.getStartTime()
        // this.startTime = this.$route.query.startTime
        // console.log(this.$route.query)
    },
    mounted(){
        // this._changeCountDown();
        console.log(this.startTime,this.countDown)
    }
}
</script>